          { id: 'CategoriaLiquidados', label: 'Cargos Liquidados', type: 'number' }
        ],
        rows: [
          {
            c: [
              { v: 0, f: 'Cat 1 CCT 366/06 Femenino' },
              { v: this.categoriaData.cat1.fem.habilitados, f: this.categoriaData.cat1.fem.habilitados },
              { v: this.categoriaData.cat1.fem.liquidados, f: this.categoriaData.cat1.fem.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 1 CCT 366/06 Masculino' },
              { v: this.categoriaData.cat1.masc.habilitados, f: this.categoriaData.cat1.masc.habilitados },
              { v: this.categoriaData.cat1.masc.liquidados, f: this.categoriaData.cat1.masc.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: '' },
              { v: 0, f: 0 },
              { v: 0, f: 0 }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 2 CCT 366/06 Femenino' },
              { v: this.categoriaData.cat2.fem.habilitados, f: this.categoriaData.cat2.fem.habilitados },
              { v: this.categoriaData.cat2.fem.liquidados, f: this.categoriaData.cat2.fem.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 2 CCT 366/06 Masculino' },
              { v: this.categoriaData.cat2.masc.habilitados, f: this.categoriaData.cat2.masc.habilitados },
              { v: this.categoriaData.cat2.masc.liquidados, f: this.categoriaData.cat2.masc.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: '' },
              { v: 0, f: 0 },
              { v: 0, f: 0 }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 3 CCT 366/06 Femenino' },
              { v: this.categoriaData.cat3.fem.habilitados, f: this.categoriaData.cat3.fem.habilitados },
              { v: this.categoriaData.cat3.fem.liquidados, f: this.categoriaData.cat3.fem.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 3 CCT 366/06 Masculino' },
              { v: this.categoriaData.cat3.masc.habilitados, f: this.categoriaData.cat3.masc.habilitados },
              { v: this.categoriaData.cat3.masc.liquidados, f: this.categoriaData.cat3.masc.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: '' },
