              { v: this.categoriaData.cat4.masc.liquidados, f: this.categoriaData.cat4.masc.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: '' },
              { v: 0, f: 0 },
              { v: 0, f: 0 }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 5 CCT 366/06 Femenino' },
              { v: this.categoriaData.cat5.fem.habilitados, f: this.categoriaData.cat5.fem.habilitados },
              { v: this.categoriaData.cat5.fem.liquidados, f: this.categoriaData.cat5.fem.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 5 CCT 366/06 Masculino' },
              { v: this.categoriaData.cat5.masc.habilitados, f: this.categoriaData.cat5.masc.habilitados },
              { v: this.categoriaData.cat5.masc.liquidados, f: this.categoriaData.cat5.masc.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: '' },
              { v: 0, f: 0 },
              { v: 0, f: 0 }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 6 CCT 366/06 Femenino' },
              { v: this.categoriaData.cat6.fem.habilitados, f: this.categoriaData.cat6.fem.habilitados },
              { v: this.categoriaData.cat6.fem.liquidados, f: this.categoriaData.cat6.fem.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 6 CCT 366/06 Masculino' },
              { v: this.categoriaData.cat6.masc.habilitados, f: this.categoriaData.cat6.masc.habilitados },
              { v: this.categoriaData.cat6.masc.liquidados, f: this.categoriaData.cat6.masc.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: '' },
              { v: 0, f: 0 },
              { v: 0, f: 0 }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 7 CCT 366/06 Femenino' },
              { v: this.categoriaData.cat7.fem.habilitados, f: this.categoriaData.cat7.fem.habilitados },
              { v: this.categoriaData.cat7.fem.liquidados, f: this.categoriaData.cat7.fem.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 7 CCT 366/06 Masculino' },
              { v: this.categoriaData.cat7.masc.habilitados, f: this.categoriaData.cat7.masc.habilitados },
              { v: this.categoriaData.cat7.masc.liquidados, f: this.categoriaData.cat7.masc.liquidados }
            ]
          }
        ]
      }
    },
    categoriaData () {
      // "Cat 1 CCT 366/06"
      // "Cat 2 CCT 366/06"
      // "Cat 3 CCT 366/06"
      // "Cat 4 CCT 366/06"
      // "Cat 5 CCT 366/06"
      // "Cat 6 CCT 366/06"
      // "Cat 7 CCT 366/06"
      return this.nodocentesCargos
        .reduce(
          (acc, cur) => {
            const subr1 = new RegExp('SubR: Cat 1', 'gm')
            const subr2 = new RegExp('SubR: Cat 2', 'gm')
            const subr3 = new RegExp('SubR: Cat 3', 'gm')
            const subr4 = new RegExp('SubR: Cat 4', 'gm')
            const subr5 = new RegExp('SubR: Cat 5', 'gm')
            const subr6 = new RegExp('SubR: Cat 6', 'gm')
            const cate1 = cur.descripcion_categoria === 'Cat 1 CCT 366/06'
            const cate2 = cur.descripcion_categoria === 'Cat 2 CCT 366/06'
            const cate3 = cur.descripcion_categoria === 'Cat 3 CCT 366/06'
            const cate4 = cur.descripcion_categoria === 'Cat 4 CCT 366/06'
            const cate5 = cur.descripcion_categoria === 'Cat 5 CCT 366/06'
            const cate6 = cur.descripcion_categoria === 'Cat 6 CCT 366/06'
            const cate7 = cur.descripcion_categoria === 'Cat 7 CCT 366/06'
            const FEM = cur.sexo === 'F'
            const parseLiquidados = (cat, cargo) => ({
              ...cargo,
              [cat]: FEM
                ? {
                  masc: cargo[cat].masc,
                  fem: { habilitados: cargo[cat].fem.habilitados, liquidados: cargo[cat].fem.liquidados + 1 }
                }
                : {
                  fem: cargo[cat].fem,
                  masc: { habilitados: cargo[cat].masc.habilitados, liquidados: cargo[cat].masc.liquidados + 1 }
                }
            })
            const parseHabilitados = (cat, cargo) => ({
              ...cargo,
              [cat]: FEM
                ? {
                  masc: cargo[cat].masc,
                  fem: { liquidados: cargo[cat].fem.liquidados, habilitados: cargo[cat].fem.habilitados + 1 }
                }
                : {
                  fem: cargo[cat].fem,
                  masc: { liquidados: cargo[cat].masc.liquidados, habilitados: cargo[cat].masc.habilitados + 1 }
                }
            })
            return subr1.test(cur.subrogancia)
              ? parseLiquidados('cat1', acc)
              : subr2.test(cur.subrogancia)
                ? parseLiquidados('cat2', acc)
                : subr3.test(cur.subrogancia)
                  ? parseLiquidados('cat3', acc)
                  : subr4.test(cur.subrogancia)
                    ? parseLiquidados('cat4', acc)
                    : subr5.test(cur.subrogancia)
                      ? parseLiquidados('cat5', acc)
                      : subr6.test(cur.subrogancia)
                        ? parseLiquidados('cat6', acc)
                        : cate1
                          ? parseHabilitados('cat1', acc)
                          : cate2
                            ? parseHabilitados('cat2', acc)
                            : cate3
                              ? parseHabilitados('cat3', acc)
                              : cate4
                                ? parseHabilitados('cat4', acc)
                                : cate5
                                  ? parseHabilitados('cat5', acc)
                                  : cate6
                                    ? parseHabilitados('cat6', acc)
                                    : cate7
                                      ? parseHabilitados('cat7', acc)
                                      : acc
          },
          {
            cat1: { masc: { habilitados: 0, liquidados: 0 }, fem: { habilitados: 0, liquidados: 0 } },
            cat2: { masc: { habilitados: 0, liquidados: 0 }, fem: { habilitados: 0, liquidados: 0 } },
            cat3: { masc: { habilitados: 0, liquidados: 0 }, fem: { habilitados: 0, liquidados: 0 } },
            cat4: { masc: { habilitados: 0, liquidados: 0 }, fem: { habilitados: 0, liquidados: 0 } },
            cat5: { masc: { habilitados: 0, liquidados: 0 }, fem: { habilitados: 0, liquidados: 0 } },
            cat6: { masc: { habilitados: 0, liquidados: 0 }, fem: { habilitados: 0, liquidados: 0 } },
            cat7: { masc: { habilitados: 0, liquidados: 0 }, fem: { habilitados: 0, liquidados: 0 } }
          }
        )
    }
  },
  methods: {
    parseCategoria (v) {
      return v === 'cat1'
        ? 'Cat 1 CCT 366/06'
        : v === 'cat2'
          ? 'Cat 2 CCT 366/06'
          : v === 'cat3'
            ? 'Cat 3 CCT 366/06'
            : v === 'cat4'
              ? 'Cat 4 CCT 366/06'
              : v === 'cat5'
                ? 'Cat 5 CCT 366/06'
                : v === 'cat6'
                  ? 'Cat 6 CCT 366/06'
                  : v === 'cat7'
                    ? 'Cat 7 CCT 366/06'
                    : v
    }
  }
}
</script>
